import { UserType } from '../reducers/userReducer';
import {
    HANDLE_USER_CHANGE,
    FETCH_USER,
    ADMIN_LOADING
} from './type'

const  createData = (user_id: string, name: string, roles: string, brand: string[], area: string) => {
    return {user_id, name, roles, brand, area };
}
  
const initMockData = [
    createData('1', 'welly.zhang', 'Viewer', ['KFC', 'Pizza Hut', 'PHD'], 'Hong Kong'),
    createData('2', 'willy.lu', 'Admin', ['Pizza Hut', 'PHD'], 'Hong Kong'),
    createData('3', 'yt.liao', 'Viewer', ['KFC', 'PHD'], 'Hong Kong'),
    createData('4', 'ben.kuo', 'Viewer', ['KFC', 'Pizza Hut' ], 'Hong Kong'),      
];

export const handleUserChange = (user: UserType, changePattern: object) => async (dispatch:any) => {

    const payload = { ...user, ...changePattern }
    console.log(payload)
    dispatch({
        type:HANDLE_USER_CHANGE,
        payload:payload
    })
}

export const fetchUser = () => async (dispatch: any) =>
{
    dispatch({ type: ADMIN_LOADING, payload: true })
    
    const payload = initMockData
    //const payload = await web.get(`/v1/masterdata/genders?${bannerFetch}market=${market}&startdate=${startDate}&enddate=${endDate}&comparestartdate=${compareStartDate}&compareenddate=${compareEndDate}`)
    await new Promise(r => setTimeout(r, 300));
    dispatch({
        type: FETCH_USER,
        payload: payload
    })
    
    dispatch({ type: ADMIN_LOADING, payload: false })

    return payload
}